import React from 'react';
import { Link } from 'react-router-dom';


const Account = () => {
  return (
    <div className="wrapper">
      <form action="">
        <h1>Mi cuenta</h1>
        <div className="input-box">
          <label className="label-account" htmlFor="">Usuario: </label>
          <input type="text" placeholder="JuanRi27864" readOnly />
        </div>
        <div className="input-box">
          <label className="label-account" htmlFor="">Contraseña: </label>
          <input type="password" placeholder="*****" readOnly />
        </div>
        
      </form>
          <button type="button" className="btn"><a href='/'>Cerrar Sesión</a></button>
    </div>
  );
};

export default Account;
