import React from 'react';
import InputBox from './InputBox';
import { Link } from 'react-router-dom';




const Form = () => {
  return (
    <div className="wrapper">
      <form action="">
        <h1>Ingresar</h1>
        <InputBox type="text" placeholder="Usuario" iconClass='bx bxs-user' />
        <InputBox type="password" placeholder="Contraseña" iconClass='bx bxs-lock-alt' />
        <div className="remember-forgot">
          <a href="#">Olvidé mi contraseña</a>
        </div>
        
        
        <div className="register-link">
          <p>¿No tienes una cuenta? <a href="/registro">Registrarse</a></p>
        </div>
      </form>
        <button type="submit" class="btn"><a href ="/cuenta">Ingresar</a></button>
    </div>
  );
};

export default Form;
